.landing_text-snowflake {
	font: 700 14.29rem/1.3 $gotham-bold;
	letter-spacing: -0.04em;

	@include on(small-desktop) {
		font-size: 10.71rem;
	}

	@include on(tablet) {
		font-size: 8.57rem;
	}
}

.landing_text-heading-1 {
	font: 700 5.14rem/1.2 $gotham-bold;
	letter-spacing: -0.04em;

	@include on(small-desktop) {
		font-size: 4rem;
	}

	@include on(tablet) {
		font-size: 3.43rem;
	}

	@include on(mobile) {
		font-size: 2.86rem;
	}
}

.landing_text-content-block {
	> .preamble {
		font: 500 0.86rem/1 $gotham-medium;
		letter-spacing: -0.01em;
		margin-bottom: $spacing-secondary;
	}

	> .title {
		font: 700 3.43rem/1.2 $gotham-bold;
		letter-spacing: -0.03em;
	}

	> .description {
		font: 600 1.29rem/1.4 $gilroy-semibold;
		letter-spacing: -0.02em;
		margin-top: 26px;
	}

	> .link {
		font: 700 1rem/1.5 $gilroy-bold;
		margin-top: $spacing-triple;
		display: flex;
		align-items: center;
		cursor: pointer;

		> .icon {
			min-width: 24px;
			height: 24px;
		}
	}

	@include on(small-desktop) {
		> .preamble {
			font-size: 0.86rem;
		}

		> .title {
			font-size: 2.29rem;
		}

		> .description {
			font-size: 1.29rem;
		}
	}

	@include on(mobile) {
		> .title {
			font-size: 1.71rem;
		}

		> .description {
			font-size: 1.14rem;
			margin-top: $spacing-double;
		}
	}
}

// START - INVESTMENT PROFILE

@mixin landing_use-case-bubble--large-desktop--investment-profile--details--wrapper--fund-name {
	> .preamble {
		font-size: 0.741rem;
	}

	> .performance {
		font-size: 0.741rem;
		margin-left: 13.82px;

		> .arrow-up {
			min-width: 6.91px;
			height: 6.91px;
			margin-right: 3.46px;
		}
	}
}

@mixin landing_use-case-bubble--large-desktop--investment-profile--details--wrapper {
	> .fund-name {
		@include landing_use-case-bubble--large-desktop--investment-profile--details--wrapper--fund-name;
	}

	> .product-name {
		font-size: 0.72rem;
		margin-top: 3.46px;
	}
}

@mixin landing_use-case-bubble--large-desktop--investment-profile--details {
	padding: 10px 12px 10px 14px;

	> .wrapper {
