	}

	> .icon {
		margin-left: 7px;
		min-width: 27.65px;
		height: 27.65px;
	}
}

@mixin landing_use-case-bubble--large-desktop--investment-profile--loanable {
	padding: 14px 14px 10px;
	border-radius: 6.91px 6.91px 0 0;

	> .title {
