@mixin landing_use-case-bubble--mobile--investment-profile {
	max-width: 166px;
	box-shadow: 0px 4.88794px 7.33192px -4.88794px rgba(15, 37, 69, 0.2);
	border-radius: 4.89px;

	> .loanable {
		@include landing_use-case-bubble--mobile--investment-profile--loanable;
	}

	> .details {
		padding: 7px 8px 7px 10px;

		> .wrapper {
			> .fund-name {
				@include landing_use-case-bubble--mobile--investment-profile--fund-name;
			}

			> .product-name {
				font-size: 0.5rem;
				margin-top: 2.44px;
			}
