}

@mixin landing_use-case-bubble--investment-profile--fund-name {
	width: 100%;
	display: flex;
	align-items: center;

	> .preamble {
		font: 400 0.86rem/1.3 $gotham-bold;
		color: #a1a8b4;
		letter-spacing: -0.03em;
		text-transform: uppercase;
	}

	> .performance {
		font: 400 0.86rem/1.3 $gotham-bold;
		display: flex;
		align-items: center;
		color: #11b051;
		margin-left: $spacing-double;

		> .arrow-up {
			width: 8px;
			height: 8px;
			margin-right: $spacing-half;
		}
	}
}

@mixin landing_use-case-bubble--investment-profile {
	color: #2044a5;
	max-width: 272px;
	background: #ffffff;

	> .loanable {
		padding: $spacing-double $spacing-double $spacing-secondary;
		background: #eef4ff;
		border-radius: 8px;

		> .title {
			font: 400 1rem/1 $gotham-bold;
			opacity: 0.8;
			letter-spacing: -0.01em;
		}

		> .amount {
			font: 700 2.29rem/1 $gotham-bold;
			letter-spacing: -0.02em;
			display: flex;
			align-items: center;
			margin-top: $spacing-default;

			> .currency {
				width: 23px;
				height: 28px;
				margin-right: $spacing-half;
			}
		}
	}

	> .details {
		border-top: 1px solid #d3dff7;
		background: #ffffff;
		padding: $spacing-secondary 14px $spacing-secondary $spacing-double;
		display: flex;

		> .wrapper {
			width: 100%;

			> .fund-name {
				@include landing_use-case-bubble--investment-profile--fund-name;
			}

			> .product-name {
				margin-top: $spacing-half;
				letter-spacing: -0.01em;
				font: 700 0.86rem/1.3 $gotham-bold;
			}
		}

		> .icon {
			margin-left: $spacing-default;
			display: block;
			min-width: 32px;
			height: 32px;
		}
	}

	> .chart {
		> svg {
			width: 100%;
			height: 82.35px;
		}

		> .label {
			font: 600 0.57rem/1.5 $gilroy-semibold;
			color: #a1a8b4;
			padding-left: $spacing-double;
		}
	}
}

// END - INVESTMENT PROFILE

// START - WMP

