// START - BUBBLE

@mixin landing_use-case-bubble--large-desktop {
	box-shadow: 0 6.72821px 10.0923px -6.72821px rgba(15, 37, 69, 0.2);
	border-radius: 7px;

	&.-wmp {
		@include landing_use-case-bubble--large-desktop--wmp;
	}

	&.-investment-profile {
		@include landing_use-case-bubble--large-desktop--investment-profile;
	}

	&.-total-savings {
		@include landing_use-case-bubble--large-desktop--total-savings;
	}
}

@mixin landing_use-case-bubble--small-desktop--amount-received {
	max-width: 151px;
	height: 50.23px;
	border-radius: 4.64615px;
	padding: $spacing-secondary 14px 9px;

	> .label {
		font-size: 0.57rem;
	}

	> .amount {
		font-size: 1.36rem;
		margin-top: $spacing-quarter;

		> .currency {
			width: 13.21px;
			height: 16.52px;
			padding: 0.5px 0 1.5px;
			margin-right: $spacing-quarter;
		}
