}

@mixin landing_use-case-bubble--small-desktop {
	&.-amount-received {
		@include landing_use-case-bubble--small-desktop--amount-received;
	}

	&.-investment-profile {
		@include landing_use-case-bubble--small-desktop--investment-profile;
	}

	&.-wmp {
		@include landing_use-case-bubble--small-desktop--wmp;
	}

	&.-total-savings {
		@include landing_use-case-bubble--small-desktop--total-savings;
	}
}

@mixin landing_use-case-bubble--tablet {
	&.-wmp {
