			height: 22px;
		}
	}

	&.-total-savings {
		@include landing_use-case-bubble--tablet--total-savings;
	}
}

@mixin landing_use-case-bubble--mobile--amount-received {
	max-width: 127px;
	height: 42.54px;
	border-radius: 3.90769px;
	padding: 9px $spacing-secondary $spacing-default;

