			letter-spacing: -0.04em;
			color: #0e517c;
			margin-top: $spacing-half;
		}
	}
}

.landing_shop-item {
	width: 100%;
	border-radius: 12px;
	display: flex;
	justify-content: end;
	align-items: end;

	> .content {
		@include landing_shop-item--content;
	}

	&.-watch,
	&.-tote {
		max-width: 329px;
	}

	&.-watch {
		@include landing_shop-item--watch;
	}

	&.-tote {
		@include landing_shop-item--tote;
	}

	&.-headphones {
		@include landing_shop-item--headphones;
	}

	&.-cream {
		@include landing_shop-item--cream;
	}

	&.-vase {
		@include landing_shop-item--vase;
	}

	@include on(small-desktop) {
		@include landing_shop-item--small-desktop;
	}

	@include on(mobile) {
		@include landing_shop-item--mobile;
	}
}

// END  - SHOP ITEM

.landing_text-display {
	font: 700 6.57rem/1.2 $gotham-bold;
	letter-spacing: -0.04em;

	@include on(small-desktop) {
		font-size: 5.36rem;
	}

	@include on(tablet) {
		font-size: 3.43rem;
	}
}

.landing_use-case-tabs {
	display: flex;

	> .item {
		border-bottom: 2px solid #ffffff;
		padding-bottom: 30px;
		color: #ffffff;
		cursor: pointer;

		&:not(:first-of-type) {
			margin-left: $spacing-quad;
		}

		> .icon {
			width: 40px;
			height: 40px;
			display: block;
			margin: 0 auto;
		}

		> .title {
			margin-top: 6px;
			font: 700 1.14rem/1 $gotham-bold;
			letter-spacing: -0.01em;
		}
	}
}

// START  - SECTION-9 BUBBLE

@mixin landing_section-9-bubble--small-desktop {
	max-width: 552px;
	padding: 22px 9px 20px 22px;

	> .preamble {
		font-size: 1.01rem;
	}

	> .content {
		> .price {
			font-size: 3.24rem;

			> .currency {
				width: 32px;
				height: 45px;
			}
		}

		> .btn {
			width: 118px;
			font-size: 1.42rem;
		}
	}
}

@mixin landing_section-9-bubble--tablet {
	max-width: 480px;
	padding: $spacing-triple 68px 22px 17px;

	> .preamble {
		font-size: 0.77rem;
	}

	> .content {
		> .price {
			font-size: 2.46rem;

			> .currency {
				width: 25px;
				height: 34px;
				margin-right: $spacing-default;
			}
		}

		> .btn {
			width: 89px;
			font-size: 1.07rem;
		}
	}
}

@mixin landing_section-9-bubble--mobile {
	max-width: 366px;
	padding: 18px 51px 17px 13px;

	> .preamble {
		font-size: 0.59rem;
	}

	> .content {
		> .price {
			font-size: 1.88rem;

			> .currency {
				width: 19px;
				height: 26px;
				margin-left: $spacing-half;
			}
		}

		> .btn {
			width: 68px;
			font-size: 0.82rem;
		}
	}
}

@mixin landing_section-9-bubble--content {
	width: 100%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: 9px;

	> .price {
		font: 500 3.58rem/1 $gotham-medium;
		letter-spacing: -0.02em;
		display: flex;
		align-items: center;
		color: #f2f8ff;

		> .currency {
			width: 36px;
			height: 50px;
			display: inline-block;
			margin-right: $spacing-default;
		}
	}

	> .btn {
		font: 700 1.57rem/1 $gotham-bold;
		letter-spacing: -0.03em;
		width: 130px;
		padding: 13px 0px 9px;
		background: #ddeeff;
		color: #0057e4;
		border-radius: 25px;
		text-align: center;
	}
}

.landing_section-9-bubble {
	width: 100%;
	max-width: 611px;
	padding: 25px 11px 22px 25px;
	background: #1972f9;
	border-radius: 12.58px;

	> .preamble {
		font: 700 1.12rem/1 $gotham-bold;
		letter-spacing: 0.1em;
		text-transform: uppercase;
		color: rgba(242, 248, 255, 0.8);
	}

	> .content {
		@include landing_section-9-bubble--content;
	}

	@include on(small-desktop) {
		@include landing_section-9-bubble--small-desktop;
	}

	@include on(tablet) {
		@include landing_section-9-bubble--tablet;
	}

	@include on(mobile) {
		@include landing_section-9-bubble--mobile;
	}
}

// END  - SECTION-9 BUBBLE
